import * as React from 'react';
import * as ReactDOM from 'react';
import { BrowserRouter, Route, Link } from 'react-router-dom';
import { Container, Card, Grid } from 'semantic-ui-react';
import MarkdownViewer from './front-end-libraries/markdown-viewer/index';
import Calculattor from './front-end-libraries/javascript-calculator/index';
import QuoteMachine from './front-end-libraries/quote-machine/index';


import { showcaseItems } from './constants'

import './global.css';

const ShowCaseCard = (props) => {
  return (
    <Card
      as={Link}
      to={props.header}
      image={props.image}
      header={props.header}
      description={props.description}
    />
  );
}

const generateShowCaseItems = (items) => {
  return items.reduce(
    (acc, item) => {
      acc.push(<ShowCaseCard {...item} />)
      return acc;
    },
    []
  );
}

const Home = (props) => {
  return (
    <Card.Group>
      {generateShowCaseItems(showcaseItems)}
    </Card.Group>
  );
}

export default class App extends React.Component {
  render() {
    return (
      <BrowserRouter>
        <Container id='content-container' style={{ backgroundColor: "white", minHeight: "100vh" }}>
          <Route key="Home" exact path="/" component={Home} />
          <Route key="markdownViewer" exact path="/markdownViewer" component={MarkdownViewer} />
          <Route key="calcualtor" exact path="/calcualtor" component={Calculattor} />
          <Route key="randomQuoteMachine" exact path="/randomQuoteMachine" component={QuoteMachine} />
        </Container>
      </BrowserRouter>
    );
  }

}
